import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-rtb-share',
  templateUrl: './rtb-share.component.html',
  styleUrls: ['./rtb-share.component.less']
})
export class RtbShareComponent implements OnInit {

  newEmail: string

  emailsCount: number

  constructor() { }

  ngOnInit() {
  }

  generateString(len) {
    let str = "";
    var chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (let index = 0; index < len; index++) {
      str += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return str;
  }

  addEmail() {
    this.newEmail = `${this.generateString(5)}@${this.generateString(4)}.${this.generateString(2)}`;
  }

  emailsChanged(e) {
    this.emailsCount = e;
  }
  getEmailsCount() {
    alert(this.emailsCount);
  }
}
